.dashboard {
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(180px, auto);
  grid-auto-flow: dense;
  padding-top: 20px; /* Adds space at the top of the dashboard */
}

@media (max-width: 1200px) { /* Assuming 'xl' breakpoint */
  .dashboard {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 992px) { /* Assuming 'lg' breakpoint */
  .dashboard {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) { /* Assuming 'md' breakpoint */
  .dashboard {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media (max-width: 576px) { /* Assuming 'sm' breakpoint */
  .dashboard {
    grid-auto-rows: minmax(120px, auto);
  }
}

.box {
  padding: 20px;
  border-radius: 10px;
  border: 2px solid #f0f0f0; /* Replace '#f0f0f0' with your $soft-bg variable color */
}

.box1, .box4 {
  grid-column: span 1;
  grid-row: span 3;
}

.box7 {
  grid-column: span 2;
  grid-row: span 2;
}

@media (max-width: 768px) { /* Assuming 'md' breakpoint */
  .box7 {
    display: none;
  }
}
